
.container-bblue {
  background-color: #001f3d; /* Example background color for the blue section */
  padding: 30px;
}

.edu {
  font-size: 14px;
  color: #666; /* Text color for the descriptions */
}

.marketing-section {
  background-size: cover; /* Ensures the image covers the entire container */
  background-position: center; /* Centers the background image */
  height: 500px; /* Height for the section with the background image */
  padding: 115px 0; /* Adds padding for spacing */
  display: flex; /* Flexbox for centering the content */
  justify-content: center; /* Centers the content horizontally */
  align-items: center; /* Centers the content vertically */
  text-align: center; /* Centers the text */
  width: calc(100% + 10px); /* Increase width to make the image larger on the right */
  margin-right: -5px; /* Adjust margin to prevent overflow */
}

.card-body {
  background-color: rgba(0, 2, 0, 0.58); /* Adds a slight dark overlay for text contrast */
  padding: 10px;
  border-radius: 0px;
}

h3 {
  font-size: 24px;
  color: white; /* Makes the title stand out */
}

p {
  font-size: 20px;
  padding-right: -100px;
  color: white; /* Makes the description text stand out */
}

.topbar {
  position: inherit;
  width: 100%;
  flex-direction: row;
  height: 10%;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: flex-end; /* Align items to the right */
  padding: 25px; /* Add padding for better spacing */
}

.text-container {
  text-align: right; /* Align text to the right */
}

.service-item {
  color: #FFFF00; /* Text color */
  margin: 0.3em; /* Uniform margin for each item */
}

.center {
  align-items: center;
}

.btn {
  background-color: #007bff; /* Button color */
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.btn:hover {
  background-color: #0056b3; /* Darkens the button on hover */
}

@media (max-width: 768px) {
  .marketing-section {
      padding: 20px;
  }
  .card {
      height: auto; 
  }
  .card-body p {
      margin-left: 0; 
      margin-right: 0; 
      text-align: left;
  }
}
